import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FileUnZipListResponse } from "../../Model/Response/FileUnZipListResponse";
import { FileUnZipResponse } from "../../Model/Response/FileUnZipResponse";
import { FileUnZipViewModel } from '../../Model/ViewModel/FileUnZipViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FileUnZipServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FileUnZipServicesProvider extends FileServicesProvider {
  private FileUnZips: FileUnZipListResponse;
  private FileUnZip: FileUnZipResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FileUnZip";
    console.log("Hello FileUnZipServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FileUnZip?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FileUnZip/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FileUnZips = new FileUnZipListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FileUnZips;
    });
  }



  public PostPutAsync(FileUnZipViewModel:FileUnZipViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FileUnZip";
    else
    sub_url = "FileUnZip/{1}";
    sub_url = String.Format(sub_url, this.ctl,FileUnZipViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FileUnZipViewModel,mode).map(
      item => {
        this.FileUnZip = new FileUnZipResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FileUnZip;
      }
    );
  }
}



